.card {
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background: #fff;
}

.card:hover .card__headline {
  color: #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 32px 19px 40px;
}

.card__title {
  padding: 0 16px;
}

.card__title-headline {
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title-code {
  color: #616070;
  margin: 0;
  padding: 4px 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__title .card__price {
  justify-content: space-between;
  margin: 24px 0 16px;
  display: flex;
}

.card__title .card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__title .card__price-amount {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__module {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__module-review {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__module .stars {
  margin: 0;
  display: flex;
}

.card__module .stars-star {
  background: url("star-active.edda4c43.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__module .stars-star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

@media screen and (width >= 488px) {
  .container {
    grid-template: 1fr 1fr / 1fr 1fr;
  }
}

@media screen and (width >= 768px) {
  .container {
    grid-template: 1fr 1fr 1fr / 1fr 1fr 1fr;
  }
}

@media screen and (width >= 1024px) {
  .container {
    grid-template: 1fr 1fr 1fr 1fr / 1fr 1fr 1fr 1fr;
    padding: 50px 40px;
  }
}

@media screen and (width >= 1200px) {
  .container {
    padding: 50px 128px;
  }
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  display: flex;
}

.header__logo {
  margin-left: 50px;
}

.nav {
  height: 60px;
  margin: 0 50px 0 0;
}

.nav a.is-active {
  color: #00acdc;
}

.nav a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  display: block;
  position: absolute;
  top: 56px;
}

.nav__list {
  justify-content: space-between;
  margin: 19px 0 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list li {
  margin-right: 20px;
}

.nav__list a {
  color: #060b35;
  text-transform: uppercase;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  text-decoration: none;
}

.nav__list a:hover {
  color: #00acdc;
}

.nav__list li:nth-child(8) {
  margin-right: 0;
}

.container {
  grid-gap: 46px 48px;
  justify-content: center;
  align-items: center;
  width: 944px;
  margin: 0 auto;
  display: grid;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

